import React from "react";
import './App.css';
import Navigation from "./components/Navigation/Navigation";
import Settings from "./components/Settings/Settings";
import News from "./components/News/News";
import Music from "./components/Music/Music";
import {BrowserRouter, Route} from "react-router-dom";
import HeaderContainer from "./components/Header/HeaderContainer";
import LoginContainer from "./components/Login/LoginContainer";
import {connect, Provider} from "react-redux";
import {withRouter} from "react-router";
import {compose} from "redux";
import Preloader from "./components/common/Preloader/Preloader";
import {initializeApp} from "./redux/app-reducer";
import store from "./redux/redux-store";

import ProfileContainer from "./components/Profile/ProfileContainer";
import {withSuspense} from "./components/hoc/withSuspense";
// import UsersContainer from "./components/Users/UsersContainer";
// import DialogsContainer from "./components/Dialogs/DialogsContainer";

// Lazy load components
const UsersContainer = React.lazy(() => import("./components/Users/UsersContainer"));
const DialogsContainer = React.lazy(() => import("./components/Dialogs/DialogsContainer"));


class App extends React.Component {
    componentDidMount() {
        // debugger
        this.props.initializeApp();
    }

    render() {
        // console.log(this.props);
        if (!this.props.initialized) {
            return <Preloader/>
        }

        return (
            <div className="app-wrapper">
                <HeaderContainer/>
                <Navigation/>
                <div className="app-wrapper-content">
                    <Route path='/Profile/:userId?' render={() => (
                        <ProfileContainer/>)}/>
                    <Route path='/Dialogs' render={withSuspense(DialogsContainer)}/>
                    <Route path='/Users' render={withSuspense(UsersContainer)}/>
                    <Route path='/Login' render={withSuspense(LoginContainer)}/>
                    <Route path="/News" component={News}/>
                    <Route path="/Music" component={Music}/>
                    <Route path="/Settings" component={Settings}/>
                </div>
            </div>
        );
    }
}

const mapStateToProps = (state) => {
    // console.log(state.app);
    return {
        initialized: state.app.initialized
    }
}

let AppContainer = compose(
    withRouter,
    connect(mapStateToProps, {initializeApp}))(App);

const MainApp = (props) => {
    return <BrowserRouter>
        <React.StrictMode>
            <Provider
                store={store}> {/* компонента из библеотеки react-redux прокидывает store в context (глобальная область видимости) внутри билблиотеки для дочерних компонент создаеться Consumer для того что бы взять store из context */}
                <AppContainer/>
            </Provider>
        </React.StrictMode>
    </BrowserRouter>
}

export default MainApp;