import React from "react";
import {connect} from "react-redux";
// import Login from "./Login";
// import {Field, Form} from "react-final-form";
// import { FORM_ERROR } from 'final-form'
import {LoginThunk} from "../../redux/auth-reducer";
import {Redirect} from "react-router";
// import {composeValidators, required} from "../utils/validators/validator";
// import {maxLength} from "../utils/validators/validator";
import Logi11111n from "./Logi11111n";


class LoginContainer extends React.Component {
    render() {

        if (this.props.isAuth) {
            return <Redirect to={"/Profile"} />
        }


        return (
            <Logi11111n {...this.props} />
        );
    }
}

let mapStateToProps = (state) => {
    // console.log(state.auth);
    return {
        isAuth: state.auth.isAuth
    }
}

export default connect(mapStateToProps, {LoginThunk})(LoginContainer);