import React from 'react';
import style from "./profile.module.css";
import Profile from "./Profile";
import {connect} from "react-redux";
import {getUsersStatusThunk, getUsersThunk, setUsersStatusThunk} from "../../redux/profile-reducer";
import {withRouter} from "react-router";
// import {withAuthRedirect} from "../hoc/AuthRedirect";
import {compose} from "redux";
// import Dialogs from "../Dialogs/Dialogs";

class ProfileContainer extends React.Component {

    componentDidMount() {
        // console.log(this.props);
        let userId = this.props.match.params.userId
        // console.log(this.props);
        if (!userId) {
            userId = this.props.authorizedUserId
        }

        this.props.getUsersThunk(userId);
        this.props.getUsersStatusThunk(userId);
    }

    render() {
        // debugger;
        // console.log(this.props);
        return (
            <div className={style.content}>
                <Profile profile={this.props.profile} status={this.props.status} setStatus={this.props.setUsersStatusThunk}/>
            </div>
        );
    }
}

let mapStateToProps = (state) => {
    // console.log(state.auth.id);
   return {
       profile: state.profilePage.profile,
       status: state.profilePage.status,
       authorizedUserId: state.auth.id,
       isAuth: state.auth.isAuth
   }
}


export default compose(
    connect(mapStateToProps, {getUsersThunk, getUsersStatusThunk, setUsersStatusThunk}),
    // withAuthRedirect,
    withRouter,
    // withAuthRedirect // HOC High Order Component https://www.youtube.com/watch?v=7W4PD4BN3eY&t=99s
)(ProfileContainer);


