// import {authApi, usersApi} from "../api/api";
import {authMeThunk} from "./auth-reducer";

const SET_INITIALIZED = 'app/SET_INITIALIZED';
// const SET_USER_PHOTO = 'SET_USER_PHOTO';

let initialState = {
    initialized: false,
};

const appReducer = (state = initialState, action) => {
    // console.log(action);
    switch (action.type) {

        case SET_INITIALIZED:

            // debugger
            // console.log(action);
            return {
                ...state,
                initialized: true,
            }

        default:
            return state;
    }
}

export const setInitializedSuccess = () => ({type: SET_INITIALIZED});

export const initializeApp = () => async (dispatch) => {

    let promise = dispatch(authMeThunk()); /* !!! ВАЖНО !!! authMeThunk() НУЖНО ВЫЗВАТЬ НЕ ДЕЛАТЬ ТАК authMeThunk  НУЖЕН ()*/

    await Promise.all([promise])
    dispatch(setInitializedSuccess()) /* !!! ВАЖНО !!! setInitializedSuccess() НУЖНО ВЫЗВАТЬ НЕ ДЕЛАТЬ ТАК setInitializedSuccess  НУЖЕН ()*/
    // console.log('start');
}

export default appReducer;

