import {usersApi} from "../api/api";
import {upDateObjectInArray} from "../components/utils/object-helper";

const FOLLOW = true;
const UNFOLLOW = false;
const SET_USERS = 'users/SET_USERS';
const SET_CURRENT_PAGE = 'users/SET_CURRENT_PAGE';
const USERS_COUNT = 'users/USERS_COUNT';
const TOGGLE_IN_PROGRESS = 'users/TOGGLE_IN_PROGRESS';
const TOGGLE_FOLLOWING_IS_PROGRESS = 'users/TOGGLE_FOLLOWING_IS_PROGRESS';

let initialState = {
    users: [],
    pageSize: 10,
    totalUsersCount: 0,
    currentPage: 1,
    isProgress: false,
    followingInProgress: []
};

const usersReducer = (state = initialState, action) => {
    // console.log(action.type);
    switch (action.type) {
        case FOLLOW:
            return {
                ...state,
                users: upDateObjectInArray(state.users, action.userId, "id", {followed: true})
                // users: [...state.users] тоже самое что и map ниже map пробегает по всем элементам массива и создает их
                // users: [...state.users.map(u => u)]
                // ]
            };

        case UNFOLLOW:
            return {
                ...state,
                // users: [...state.users] тоже самое что и map ниже map пробегает по всем элементам массива и создает их
                // users: [...state.users.map(u => u)]


                users: upDateObjectInArray(state.users, action.userId, "id", {followed: false})
                // users: [...state.users.map(u => {
                //     if (u.id === action.userId) {
                //         return {...u, followed: false}; // если user.id попал в if то изминить followed на false ВОЗРАЩАЕМ КОПИЮ
                //     }
                //     return u;
                // })
                // ]
            };

        case SET_USERS:
            // return {...state, users: [...state.users, ...action.users]} // дописуем новых юзеров , ...action.users]}
            return {...state, users: action.users} // ПЕРЕЗАПИСЫВАЕМ(перезатераем) юзереов , ...action.users]}

        case SET_CURRENT_PAGE: {
            return {...state, currentPage: action.currentPage}
        }
        case USERS_COUNT: {
            return {...state, totalUsersCount: action.usersCount}
        }
        case TOGGLE_IN_PROGRESS: {
            return {...state, isProgress: action.isProgress}
        }
        case TOGGLE_FOLLOWING_IS_PROGRESS: {
            return {
                ...state,
                followingInProgress: action.isFetching
                    ? [...state.followingInProgress, action.userId] //https://www.youtube.com/watch?v=iobMksCYGHE как это работает
                    : state.followingInProgress.filter(id => id !== action.userId)
            }
        }

        default:
            return state;
    }
}

/* Эти функции использует контейнерная компонента */
/* Эти функции прокидывают данные в dispatch под капотом проверяет action.type пробрасывает в combineReducers в redux-store.js а там он попадает в usersReducer выше*/

// УБРАТЬ export кторый не используеться в других файлах
export const follow = (userId) => ({type: FOLLOW, userId}); // return  {type: FOLLOW} что бы функция dispatch получила type
export const unfollow = (userId) => ({type: UNFOLLOW, userId}); // return  {type: UNFOLLOW} что бы функция dispatch получила type
export const setUsers = (users) => ({type: SET_USERS, users});
export const setCurrentPage = (currentPage) => ({type: SET_CURRENT_PAGE, currentPage});
export const setTotalUsersCount = (usersCount) => ({type: USERS_COUNT, usersCount});
export const setIsProgress = (isProgress) => ({type: TOGGLE_IN_PROGRESS, isProgress});
export const toggleFollowingProgress = (isFetching, userId) => ({type: TOGGLE_FOLLOWING_IS_PROGRESS, isFetching, userId});

// Thunks
export const getUsersThunk = (currentPage, pageSize) => async (dispatch) => {
    dispatch(setIsProgress(true));
    dispatch(setCurrentPage(currentPage))

    let data = await usersApi.getUsers(currentPage, pageSize);
    // console.log(responsive);
    dispatch(setIsProgress(false));
    dispatch(setUsers(data.items));
    dispatch(setTotalUsersCount(data.totalCount));
}

const followUnfollowFlow = async (dispatch, userId, apiMethod, actionCreator) => {
    dispatch(toggleFollowingProgress(true, userId));
    let data = await apiMethod(userId);

    if (data === 0) {
        dispatch(actionCreator(userId))
    }
    dispatch(toggleFollowingProgress(false, userId));
}

export const setFollowThunk = (userId) => async (dispatch) => {
    let apiMethod = usersApi.setFollow.bind(usersApi);

    await followUnfollowFlow(dispatch, userId, apiMethod, follow)

}

export const setUnFollowThunk = (userId) => async (dispatch) => {
    let apiMethod = await usersApi.setUnFollow.bind(usersApi);

    await followUnfollowFlow(dispatch, userId, apiMethod, unfollow)
    // dispatch(toggleFollowingProgress(true, UserId));

    // let data = await usersApi.setUnFollow(UserId);
    // if (data === 0) {
    //     dispatch(unfollow(UserId))
    // }
    // dispatch(toggleFollowingProgress(false, UserId));
}


export default usersReducer;