// import {setIsProgress, setTotalUsersCount, setUsers} from "./users-reducer";
import {authApi, usersApi} from "../api/api";

const SET_USER_DATA = 'auth/SET_USER_DATA';
const SET_USER_PHOTO = 'auth/SET_USER_PHOTO';

let initialState = {
    usedId: null,
    login: null,
    email: null,
    isAuth: false,
    photo: null
};

const authReducer = (state = initialState, action) => {
    // console.log(action);
    switch (action.type) {
        case SET_USER_DATA:
            // debugger
            // console.log(action);
            return {
                ...state,
                ...action.data,
                isAuth: action.data.isAuth
                // isAuth: true
            }

        case SET_USER_PHOTO:
            // debugger
            return {...state, ...action.photo}

        default:
            return state;
    }
}

export const setAuthUserData = (id, login, email, isAuth) => ({type: SET_USER_DATA, data: {id, login, email, isAuth}});
export const setAuthUserPhoto = (photo) => ({type: SET_USER_PHOTO, photo});
export const setLogin = (photo) => ({type: SET_USER_PHOTO, photo});

export const authMeThunk = () => async (dispatch) => {
// debugger
    let data = await authApi.authMe();
    // console.log(data);
    let {id, login, email} = data.data;

    if (data.resultCode === 0) {
        // console.log('test');
        dispatch(setAuthUserData(id, login, email, true))
    }

    let dataPhoto = await usersApi.getUser(id);
    dispatch(setAuthUserPhoto(dataPhoto.photos.small));

}

export const LoginThunk = (email, password, rememberMe, captcha) => async (dispatch) => {
    await authApi.login(email, password, rememberMe, captcha);
    dispatch(authMeThunk());
}


export const logoutThunk = () => async (dispatch) => {
    await authApi.logout();
    dispatch(setAuthUserData(null, null, null, false));
}

export default authReducer;

