import {applyMiddleware, combineReducers, compose, createStore} from "redux";
// import { composeWithDevTools } from 'redux-devtools-extension';

import profileReducer from "./profile-reducer";
import dialogsReducer from "./dialogs-reducer";
import sidebarReducer from "./sidebar-reducer";
import usersReducer from "./users-reducer";
import authReducer from "./auth-reducer";
import thunkMiddleware from "redux-thunk"
import appReducer from "./app-reducer";

let reducers = combineReducers({ // Нужно воспринимать не как связка reducer а как state
    profilePage: profileReducer, // ветки state обслуживаеться profile-reducer.js (логика в reducer)
    messagePage: dialogsReducer, // ветки state обслуживаеться dialogs-reducer.js
    sidebar: sidebarReducer, // ветки state обслуживаеться sidebar-reducer.js
    usersPage: usersReducer,  // ветки state обслуживаеться users-reducer.js
    auth: authReducer,  // ветки state обслуживаеться users-reducer.js
    app: appReducer,  // ветки state обслуживаеться users-reducer.js
    // form: formReducer, // ВАЖНО ключем выступает ОБЯЗАТЕЛЬНО значение form: ...
});

const composeEnhancers = window.__REDUX_DEVTOOLS_EXTENSION_COMPOSE__ || compose;
const store = createStore(reducers, composeEnhancers(applyMiddleware(thunkMiddleware)));

// let store = createStore(reducers, applyMiddleware(thunkMiddleware)); // Создаем redux store в нутерь передаем закомпайненый reducers.  анолг самописного store.js

export default store;


