const ADD_NEW_MESSAGE = 'profile/ADD-NEW-MESSAGE';
// const UPDATE_NEW_MESSAGE_TEXT = 'UPDATE-NEW-MESSAGE-TEXT';

let initialState = {
    messagesData: [
        {id: 1, message: "Lorem ipsum bla bla"},
        {id: 2, message: "Lorem ipsum bla bla"},
        {id: 3, message: "Lorem ipsum bla bla"},
        {id: 4, message: "Lorem ipsum bla bldasdaa"}
    ],
    dialogsData: [
        {id: 1, name: "Person 1"},
        {id: 2, name: "Person 2"},
        {id: 3, name: "Person 3"},
        {id: 4, name: "Person 4"},
        {id: 5, name: "Person 5"}
    ],
    objMessage: 'New message'
};

const dialogsReducer = (state = initialState, action) => {
    /*
     let stateCopy = {
        ...state,
        messagesData: [...state.messagesData]
    };
    // stateCopy.messagesData = {...state.messagesData} выше альтрнатива.   ...state, обязательно идет первым потому что
    state.messagesData ниже перетрет stateCopy.messagesData
    */

    switch (action.type) {
        case ADD_NEW_MESSAGE:
            return {
                ...state,
                // objMessage: '',
                messagesData: [
                    ...state.messagesData,  // копируем массив сообщений {id: 6,   вместо push()
                    {
                        id: 6,
                        message: action.message
                    }
                ]
            };
        default:

            return state;
    }
}

/* Эти функции прокидывают данные в dispatch под капотом проверяет action.type пробрасывает в combineReducers в redux-store.js а там он попадает в dialogsReducer выше*/

export const addMassageAction = (message) => {
    console.log(message);
    return {type: ADD_NEW_MESSAGE, message}
}

// export const changeMassageAction = (message) => {
//     return {type: UPDATE_NEW_MESSAGE_TEXT, objMessage: message}
// }

export default dialogsReducer;