import {addPostActionCreator} from "../../../redux/profile-reducer";
import AllPosts from "./AllPosts";
import {connect} from "react-redux";
import React from "react";
// import {Redirect} from "react-router";
import {Field, Form} from "react-final-form";
import style from './AllPosts.module.css';


/*
Контейнерная компонента передает только необходимые данные для презентпционной
 компоненте и обрабатывет данные из презентационной компоненты передавая их в reducer
*/

class AllPostsContainer extends React.Component {

    componentDidMount() {
        // console.log(this.props);
        // if (this.props.isAuth) {
        //     return <Redirect to={"/Login"}/>
        // }
    }



    // shouldComponentUpdate(nextProps, nextState) {
    //    return this.props === nextProps;
    //    // return false;
    // }


    render() {
        // debugger;

        return (
            <div className={style.wrapperPosts}>
                <Form onSubmit={formData => {
                    // console.log(formData);
                    this.props.addPostActionCreator(formData.sendPost)

                }}

                >
                    {({handleSubmit, reset, pristine, form, submitting}) => (
                        // <PostForm handleSubmit={handleSubmit} pristine={pristine} form={form}  submitting={submitting}/>
                        <form className={style.postForm}
                            onSubmit={handleSubmit}
                        >
                                {/*<Field type="text" placeholder='Send post' name="sendPost" component="textarea"/>*/}
                                <Field name="sendPost">
                                    {({ input, meta }) => (
                                        <div className={style.textarea}>
                                            <textarea {...input} placeholder="Write post" />
                                            {meta.error && meta.touched && <span>{meta.error}</span>}
                                        </div>
                                    )}
                                </Field>
                            <div >
                                <button className={style.submit} disabled={pristine || submitting}>Publish</button>
                                {/*<button onClick={form.reset && form.submitFailed}>reset</button>*/}

                            </div>
                        </form>
                    )}
                </Form>

                <AllPosts {...this.props}/>
            </div>
        );
    }
}
let mapStateToProps = (state) => {
    return {
        data: state.profilePage
    }
}

