import {profileApi, usersApi} from "../api/api";
// import {setIsProgress, setTotalUsersCount, setUsers} from "./users-reducer";

const ADD_POST = 'ADD-POST';
// const UPDATE_NEW_POST_TEXT = 'UPDATE_NEW_POST_TEXT';
const SET_USER_PROFILE = 'SET_USER_PROFILE';
const SET_CURRENT_USER_ID = 'SET_CURRENT_USER_ID';
const SET_STATUS = 'SET_STATUS';
const DELETE_POST = 'DELETE_POST';


let initialState =   {
    postsData: [
        {id: 1, post: "Lorem Ipsum is simply dummy text of the printing and typesetting industry. Lorem Ipsum has been the industry's standard dummy text ever since the 1500s, when an unknown printer took a galley of type and scrambled it to make a type specimen book. It has survived not only five centuries, but also the leap into electronic typesetting, remaining essentially unchanged. It was popularised in the 1960s with the release of Letraset sheets containing Lorem Ipsum passages, and more recently with desktop publishing software like Aldus PageMaker including versions of Lorem Ipsum.", likesCount: 10},
        {id: 2, post: "There are many variations of passages of Lorem Ipsum available, but the majority have suffered alteration in some form, by injected humour, or randomised words which don't look even slightly believable. If you are going to use a passage of Lorem Ipsum, you need to be sure there isn't anything embarrassing hidden", likesCount: 5},
    ],
    postsDataTest: [
        {id: 1, post: "Lorem Ipsum is simply dummy text of the printing and typesetting industry. Lorem Ipsum has been the industry's standard dummy text ever since the 1500s, when an unknown printer took a galley of type and scrambled it to make a type specimen book. It has survived not only five centuries, but also the leap into electronic typesetting, remaining essentially unchanged. It was popularised in the 1960s with the release of Letraset sheets containing Lorem Ipsum passages, and more recently with desktop publishing software like Aldus PageMaker including versions of Lorem Ipsum.", likesCount: 10},
    ],
    profile: {
        photos: '',
        contacts: {
            facebook: '',
            instagram: '',
            twitter: '',
            github: ''
        },
        currentId: null,
    },
    // status: ''
};

const profileReducer = (state = initialState, action) => {
    // debugger
    switch (action.type) {
        case ADD_POST: {
            let newPost = {
                id: 5,
                post: action.postMessage,
                likesCount: 0
            };

            return  {
                ...state,
                postsData: [...state.postsData, newPost], // ,newPost] вместо push()
                // postMessage: action.postMessage

            }; // делаем копию state для того что бы отработала метод connect

            // copyState.postsData = [...state.postsData]; // делаем копию state для того что бы отработала метод connect
            // copyState.postsData.push(newPost); // push в копию state
            // copyState.newPostText = ''; // обнуляем textarea в копии state
            // this._callSubscriber(this._state); // обновлять state в reducer НЕ должны это не его обязаность (single responsibility (SOLID))

            // console.log(copyState.postsData);

            // return copyState; // возращаем кописю state
        }
        // case UPDATE_NEW_POST_TEXT: {
        //     return {
        //             ...state,
        //             newPostText: action.newText
        //         };
        //
        //     // copyState.newPostText = action.newText;
        //     // this._callSubscriber(this._state);  // обновлять state в reducer НЕ должны это не его обязаность (single responsibility (SOLID))
        //     // return copyState;
        // }
        case SET_USER_PROFILE: {
            return {...state, profile: action.profile};
        }
        case SET_CURRENT_USER_ID: {
            return {...state, profile: action.currentId};
        }

        case SET_STATUS: {
            // console.log(action.status);
            return {...state, status: action.status};
        }

        case DELETE_POST: {
            return  { ...state, postsData: state.postsData.filter(post => post.id !== action.postId) };
        }
        default:
            return state;
    }
}

/* Эти функции прокидывают данные в dispatch под капотом проверяет action.type и т.д. пробрасывает в combineReducers в redux-store.js а там он попадает в profileReducer выше*/
export const addPostActionCreator = (postMessage) => ({type: ADD_POST, postMessage}); // return  {type: ADD_POST} что бы функция dispatch получила type
const setUserProfile = (profile) => ({type: SET_USER_PROFILE, profile});
// export const setCurrentUserId = (currentId) => ({type: SET_CURRENT_USER_ID, currentId});
export const setUserStatus = (status) => ({type: SET_STATUS, status});
export const deletePost = (postId) => ({type: DELETE_POST, postId});

// export const addonPostChangeActionon = (text) =>
//     ({type: UPDATE_NEW_POST_TEXT, newText: text});  // return  {type: UPDATE_NEW_POST_TEXT, newText: text}} что бы функция dispatch получила type и данные вводимые в textarea

export const getUsersThunk = (userId) => async (dispatch) => {
    let data = await usersApi.getUser(userId)
    // console.log(responsive);
    dispatch(setUserProfile(data));
}


export const getUsersStatusThunk = (userId) => async (dispatch) => {
    let response = await profileApi.getUserStatus(userId)

    dispatch(setUserStatus(response.data));
}


export const setUsersStatusThunk = (status) => async (dispatch) => {
    await profileApi.setUserStatus(status)
    dispatch(setUserStatus(status));
}


export default profileReducer;