import style from './Header.module.css';
import {NavLink} from "react-router-dom";
import userPhoto from "./../../assets/images/profile.png"
import React from "react";

const Header = (props) => {
    // console.log(props);
    // console.log(props.login);
    // debugger
    return (
        <header className={style.header}>
            <link rel="stylesheet" href="https://pro.fontawesome.com/releases/v5.10.0/css/all.css"
                  integrity="sha384-AYmEC3Yw5cVb3ZcuHtOA93w35dYTsvhLPVnYs9eStHfGJvOvKxVfELGroGkvsg+p"
                  crossOrigin="anonymous"/>

            {/*<img src="https://i.pinimg.com/originals/33/b8/69/33b869f90619e81763dbf1fccc896d8d.jpg" alt=""/>*/}
            <div className={style.loginBlock}>
                <div className={style.info}>
                    <p>{props.login}</p>
                    <img src={props.photo != null ? props.photo : userPhoto } alt=""/>
                </div>

                {
                    props.isAuth
                        ? <div> <button onClick={props.logoutThunk}>Log out</button></div>
                        : <NavLink to={"login/"}><p>Login</p></NavLink>
                }

            </div>
        </header>
    );
}

export default Header;