const Logi11111n = (props) => (

    <div>
        console.log(props);
        <Form
            onSubmit={(formData) => {
                props.LoginThunk(formData.login, formData.password, formData.rememberMe)

            }}

            validate={values => {
                const errors = {}
                if (!values.login) {
                    errors.login = 'Required'
                }
                if (!values.password) {
                    errors.password = 'Required'
                }

                return errors
            }}
            initialValues={{rememberMe: false}}

            render={({
                         submitError,
                         handleSubmit,
                         form,
                         submitting,
                         pristine,
                         values
                     }) => (
                <form
                    onSubmit={handleSubmit}
                >
                    <div>
                        <Field name="login">
                            {({input, meta}) => (
                                <div>
                                    <input {...input} type="text" placeholder="login"/>
                                    {(meta.error || meta.submitError) && meta.touched && (
                                        <span>{meta.error || meta.submitError}</span>
                                    )}
                                </div>
                            )}
                        </Field>
                        <Field name="password" validate={required}>
                            {({input, meta}) => (
                                <div>

                                    <input {...input} type="password" placeholder="Password"/>
                                    {meta.error && meta.touched && <span>{meta.error}</span>}
                                </div>
                            )}
                        </Field>
                    </div>
                    <div>
                        <label htmlFor="remember">
                            <Field id='remember' type="checkbox" name="rememberMe"
                                   component="input"/> remember me
                        </label>
                    </div>

                    <div>
                        {/*<button type="submit" disabled={pristine || submitting}>Log in</button>*/}
                    </div>
                    {submitError && <div className="error">{submitError}</div>}
                    {/*<div className="buttons">*/}
                        <button type="submit" disabled={submitting}>Log In</button>
                    {/*</div>*/}
                    <pre>{JSON.stringify(values, 0, 2)}</pre>
                </form>
            )}
        />
    </div>
)

export default Logi11111n;