import * as axios from "axios";

const instance = axios.create({
    withCredentials: true,
    baseURL: 'https://social-network.samuraijs.com/api/1.0/',
    headers: {'API-KEY': '23b10ac7-4e95-4372-a1f2-4f9e78723416'}
});

//Query to api
export const usersApi = {
    getUsers(pageNumber = 1, pageSize = 5) {
        return instance.get(`users?page=${pageNumber}&count=${pageSize}`)  // pageNumber берем не из props потомучто не успют props прейти до перересовки
            .then(response => {
                return response.data;
            });
    },

    getUser(userId = 2) {
        return profileApi.getUser(userId);
    },

    setUnFollow(userId = 1) {
        return instance.delete(`follow/${userId}`)  // pageNumber берем не из props потомучто не успют props прейти до перересовки
            .then(response => {
                return response.data.resultCode;
            });
    },

    setFollow(userId = 1) {
        return instance.post(`follow/${userId}`, {})  // pageNumber берем не из props потомучто не успют props прейти до перересовки
            .then(response => {
                return response.data.resultCode;
            });
    }
}

export const profileApi = {
    getUser(userId = 2) {
        return instance.get(`profile/${userId}`)  // pageNumber берем не из props потомучто не успют props прейти до перересовки
            .then(response => {
                return response.data;
            });
    },

    getUserStatus(userId) {
        return instance.get(`profile/status/${userId}`);
    },

    setUserStatus(status) {
        console.log(status);
        return instance.put(`profile/status`, {status: status});
    }
}

export const authApi = {
    authMe() {
        return instance.get(`auth/me`)  // pageNumber берем не из props потомучто не успют props прейти до перересовки
            .then(response => {
                return response.data;
            });
    },

    login(email, password, rememberMe) {
        return instance.post(`/auth/login/`, {email, password, rememberMe})  // pageNumber берем не из props потомучто не успют props прейти до перересовки
            .then(response => {
                return response.data.resultCode;
            });
    },

    logout() {
        return instance.delete(`/auth/login/`)  // pageNumber берем не из props потомучто не успют props прейти до перересовки
            .then(response => {
                return response.data.resultCode;
            })
    },
}
