import style from './AllPosts.module.css';
import Post from "./Post/Post";
import React from "react";

/*
 Презентационная компонента нечего не знает о state/store отправляет
 данные в контейнерную компаненту
*/

const AllPosts = React.memo(props => {
    // console.log(props.data.profile.fullName);

    // console.log(props);

    let postsElements = props.data.postsData.map(post => <Post post={post.post} likes={post.likesCount} key={post.id} fullname={props.data.profile.fullName}/>)

    return (
        <div className={style.posts}>
            <h3>My post</h3>
            {postsElements}
        </div>
    );
})

export default AllPosts;