export const upDateObjectInArray = (items, itemId, objPropertyName, newObjectProps) => {

    return items.map(u => {
        // console.log(u);
        // console.log('------');
        // console.log(itemId = 'Id');
        if (u[objPropertyName] === itemId) {
            return {...u, ...newObjectProps}; // если user.id попал в if то изминить followed на true ВОЗРАЩАЕМ КОПИЮ
        }
        return u;
    })
}
