import style from './ProfileInfo.module.css';
import userPhoto from "./../../../assets/images/profile.png"
// import ProfileStatus from "./ProfileStatus"
import React from "react";
import ProfileStatusWithHooks from "./ProfileStatusWithHooks";
// import AllPosts from "../AllPosts/AllPosts";


const ProfileInfo = (props) => {
    const globalUrl = 'https://';
    // console.log(props.profile);

    return (
        <div className={style.infoPerson}>
            <div className={style.imageWrapper}><img src={props.profile.photos.large != null ? props.profile.photos.large : userPhoto } alt=""/></div>

            {/*{console.log(props.setStatus)}*/}
            {/*<ProfileStatus status={props.status} setStatus={props.setStatus}/>*/}
            <ProfileStatusWithHooks status={props.status} setStatus={props.setStatus}/>
            <p><strong>Name:</strong> {props.profile.fullName}</p>
            <p><strong>About me:</strong>{props.profile.aboutMe}</p>
            <p><strong>My contacts:</strong>
                {props.profile.contacts.facebook ? <p><a href={globalUrl + props.profile.contacts.facebook}>Facebook</a></p> : ''}
                {props.profile.contacts.instagram ? <p><a href={globalUrl + props.profile.contacts.instagram}>Instagram</a></p> : ''}
                {props.profile.contacts.twitter ? <p><a href={globalUrl + props.profile.contacts.twitter}>Twitter</a></p> : ''}
                {props.profile.contacts.github ? <p><a href={globalUrl + props.profile.contacts.github}>Github</a></p> : ''}
            </p>
        </div>

    );
}

export default ProfileInfo;
